// styling
import styles from './style.module.scss';

// components
import Logo from '@components/Logo';
import {NavLink} from 'react-router-dom';
import SubscribeForm from '@components/SubscribeForm';

// utils
import {memo} from 'react';

// hooks
import {useLocation} from 'react-router-dom';

// constants
import {FOOTER_LINKS, SOCIAL_LINKS} from '@constants/links';

const SocialLinks = () => {
    return SOCIAL_LINKS.map((link, index) => (
        <li key={index}>
            <a className="tile tile--filled" href={link.url} target="_blank" rel="noreferrer noopener" aria-label={link.name}>
                <i className={`icon icon-${link.icon}`}/>
            </a>
        </li>
    ))
}

const FooterNav = () => {
    const location = useLocation();

    return FOOTER_LINKS.map((item, index) => (
        <div className={styles.main_nav_item} key={index}>
            <h6>{item.title}</h6>
            <ul className="d-flex flex-column g-10">
                {
                    item.links.map((item, index) => (
                        <li className="text-bold text-sm" key={index}>
                            <NavLink to={item.url}
                                  className={location.pathname === item.url  ? styles.active : ''}>
                                {item.title}
                            </NavLink>
                        </li>
                    ))
                }
            </ul>
        </div>
    ))
}

const Footer = () => {
    return (
        <footer className={styles.footer}>
            <div className="container">
                <div className={styles.main}>
                    <div className={styles.main_block}>
                        <div className="d-flex flex-column g-20">
                            <h4>Stay in the loop</h4>
                            <SubscribeForm/>
                        </div>
                        <div className="d-flex flex-column g-20">
                            <h4>Join the community</h4>
                            <ul className="d-flex flex-wrap g-15">
                                <SocialLinks/>
                            </ul>
                        </div>
                    </div>
                    <div className={styles.main_block}>
                        <div className={styles.main_about}>
                            <Logo/>
                            <p className={`${styles.text} text-bold`}>
                            VoidGate aims to simplify the NFT buying and selling process by providing a unified platform that aggregates listings from different marketplaces. Users can browse, search and filter NFTs based on different criteria and purchase them using various cryptocurrencies.
                            </p>
                        </div>
                        {/* <nav className={styles.main_nav}>
                            <FooterNav/>
                        </nav> */}
                    </div>
                </div>
                
            </div>
        </footer>
    )
}

export default memo(Footer);